body {
    background-image: url('http://acesraise.com/wp-content/uploads/2015/12/footer_lodyas.png');
    background-repeat: repeat;
    background-position: left top;
    background-attachment: fixed;
    position: relative;
    min-height: 100vh;
}

.list__container {
    display: flex;
}

.card-action {
    position: relative;
}

.card-action .link {
    position: absolute;
    display: block;
}

.foot {
    position: absolute;
    bottom: 0;
    left: 0;
    color: white;
    height: 30px;
    width: 100%;
    text-align: center;
}

.focus1 {
    transition-duration: 2s;
}

.focus1:hover {
    transform: scale(1.1);
    opacity: 0.4;
}
